@import './constants.scss';

.dropdown__cotainer {
  position: relative;
  display: inline-grid;
  gap: 8px;

  width: 224px;
  height: 74px;
}

.dropdown__label {
  color: $headingsSecondColor;
  font-size: $headingsSecondFontSize;
  font-style: normal;
  font-weight: 500;
  line-height: 1.28;
}

.dropdown__header {
  position: relative;
  z-index: 0;
  color: $headingsColor;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;

  padding: 14px 18px;

  background-color: $bgSecondColor;
  border-radius: 14px;

  & > svg {
    position: absolute;
    top: 14px;
    right: 18px;

    stroke: $headingsColor;

    transition: transform 0.5s;
  }

  &.dropdown__open > svg {
    transform: rotate(180deg);
  }
}

.dropdown__select--input {
  appearance: none;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.dropdown__list-container {
  position: relative;
  z-index: 5;
  margin-top: 4px;
  padding: 14px 8px 14px 18px;
  height: 272px;
  overflow-y: scroll;
  border-radius: 14px;
  border: 1px solid rgba(18, 20, 23, 0.05);
  background-color: $bgColor;
  box-shadow: 0px 4px 36px 0px rgba(0, 0, 0, 0.02);
}

.dropdown__list {
  display: grid;
  gap: 8px;
  background: $bgColor;
}

.dropdown__item {
  color: rgba(18, 20, 23, 0.2);
  font-size: $standartSize;
  font-weight: 500;
  line-height: 1.25;

  background-color: $bgColor;

  cursor: pointer;

  transition: color 0.5s;

  &:hover,
  &:focus {
    color: inherit;
  }
}
